body {
  background-color: #eae7dc;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
html {
  font-size: 1rem;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.navbar{
 background-color:  #000;
}
.form-group input, .custom-file-label{
background-color: #f4f6fa;
}
.form-group{
margin-top: 1.5rem;
}
.maincakedetails{
  background-color: #eae7dc;
}
.containercakedetails
{
  background: hsla(0,0%,100%,.37);
}
.cartimag{
  width: 50%;
}
.cartdetailsholder{
  width: 50%;
}
.ingridentcontainer
{ width: auto;
  background: #eee;
}
.ingredentitem
{
  padding: 15px 5px;
    text-align: center;
   margin-right: 2rem;
   
}
.btnaddtocart{
  margin-top: 5rem;
}
.flex-even {
  flex: 1;
}
.cart_container{
width: 100%;
margin-top: 0rem;
}
.cart_itm_details_container{
  width: 70%;
}
.cart_itm_summary_container{
  background-color: #f5f5f5;
width: 30%;
}
.border_dark{
 
 border-bottom: 1px solid #c4c4c4;
}
.imageholder {
                   
  margin: 0;
  padding: 0;
  overflow: hidden;
}
.gallary_img{
  -webkit-transform: scale(1);
transform: scale(1);
-webkit-transition: .4s ease-in-out;
transition: .4s ease-in-out;
 
}
.gallary_img:hover  {
-webkit-transform: scale(1.1);
transform: scale(1.1);
  
}
.fancy_border:after, .fancy_border:before {
  content: "";
  position: absolute;
  width: 300px;
  display: block;
  background: url(data:image/png;base64,) no-repeat;
  background-size: 100%;
  height: 100%;
}
.status_div_pending{
  max-width: 100px;
  background-color: rgb(224, 54, 54);
  color: aliceblue;
  padding: 9px;
  
}
.status_div_complete{
  max-width: 100px;
  background-color:green;
  color: aliceblue;
  padding: 9px;
}
@media screen and (max-width: 768px) {
  .cartdetailsholder{
    width: 100%;
  }
  .cartimag{
    width: 100%;
  }
  .btnaddtocart{
    margin-top: 1rem;
  }
  .cart_container{
    width: 100%;
    margin-top: 9rem;
    }
  .cart_itm_details_container{
    width: 100%;
  }
  .cart_itm_summary_container{
    background-color: #f5f5f5;
  width: 100%;
  }
}